.symbol-info{
  margin : 0 24px 0 48px;
  display : flex;
  flex-direction: column;  

  .main {
    display : flex;
    align-items : center;
    height: 96px;
    justify-content: space-between;

    .s-left {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 60%;
      cursor: pointer;
      
      
      .symbol-name {
        font-size : 24px;
        color : #E0ECFF;
        align-items: center;
        display: flex;
        white-space: nowrap;
        img {
          margin-right: 8px;
        }
      }
      .select-icon {
        cursor: pointer;
        display: flex;
        align-items: center;
      }
      .divider-line {
        width: 1px;
        height: 32px;
        border-right: 1px solid #203B60;
      }
    }
    .kline-icon {
      display: none;
    }

    .index-area {
      display: flex;    
      justify-content: center;
      align-items: center;
      width: 90px;
      .index-area-wraper {
        text-align: center;
      }
      .rate {
        display: flex;
        align-items: center;
        img {
          width: 8px;
          margin-right: 4px;
        }
      }
      .rate.down {
        color : #CE5E5E;
      }
      .rate.up {
        color : #59AE99;
      }
      .index-value {
        font-size :24px;
        color : #E0ECFF;
        height: 32px;
      }
    }
  }
}
@media only screen and (max-width : 759px) {
  .symbol-info {
    margin : 0 24px 0 24px;
    .main {
      .kline-icon {
        display: block;
      }
    }
  }
}
