.trade-layout {
  display: grid; 
  grid-template-columns: 404px auto auto;
  grid-template-rows: 96px auto auto auto;
  grid-template-areas: "symbol-area trade-info trade-info" "account-info kline kline" "orderbar kline kline" "orderbar my-info my-info" "orderbar contract contract";
  gap: 0px;
  border-top: 1px solid #203B60;

  .orderbar {
    grid-area: orderbar;
    border-right: 1px solid #203B60;
  }
  .account-info {
    grid-area: account-info;
    border-right: 1px solid #203B60;
  }
  .symbol-area {
    grid-area: symbol-area;
    border-right: 1px solid #203B60;
  }
  .trade-info {
    grid-area: trade-info;
    height: 96px;
  }

  .kline {
    grid-area: kline;
    border-top: 1px solid #203B60;
  }
  .my-info {
    grid-area: my-info;
    border-top: 1px solid #203B60;
    z-index: 1;
  }
  .contract {
    grid-area: contract;
    border-top: 1px solid #203B60;
    border-right: 1px solid #203B60
  }
  .trades {
    grid-area: trades;
    border-top: 1px solid #203B60;
  }
}

//小于375px
@media only screen and (min-width: 0px) {
  .trade-layout {
    grid-template-columns: 100%;
    grid-template-areas: "symbol-area" "trade-info" "account-info" "orderbar" "my-info" "contract" ;
    grid-template-rows: 96px auto auto auto auto auto auto auto ;
  } 
  .trade-layout.mobile-trade-layout {
    grid-template-areas: "symbol-area" "kline" "orderbar"  "my-info" "contract" ;
  }
  .symbol-area {
    border-right: 0;
    // border-top :1px solid #203B60 ;
    border-bottom: 1px solid #203B60;
  }
  .kline {
    border-top: 0;
  }
}
//小于760 大于375px
@media only screen and (min-width : 375px) {
  .trade-layout.mobile-trade-layout {
    grid-template-areas: "symbol-area" "kline" "orderbar"  "my-info" "contract" ;
  }
  .trade-layout {
    grid-template-columns: 100%;
    grid-template-areas: "symbol-area" "trade-info" "account-info" "orderbar"  "my-info" "contract" ;
    grid-template-rows: 96px auto auto auto auto auto auto auto;

    .kline {
      border-top: 0;
    }

    .symbol-area {
      border-right: 0;
      // border-top :1px solid #203B60 ;
      border-bottom: 1px solid #203B60;
    }
    .orderbar {
      border-right: 0;
    }
    .contract {
      border-right: 0;
    }
    .my-info, .trade-info,.kline,.account-info{
      overflow: hidden;
    }

  }
}
//小于1140 大于760
@media only screen and (min-width : 760px) {
  .trade-layout {
    grid-template-columns: 404px auto auto;
    grid-template-rows: 96px auto auto auto;
    grid-template-areas: "symbol-area trade-info trade-info" "account-info kline kline" "orderbar kline kline" "orderbar my-info my-info" "orderbar contract contract";
    .symbol-area {
      border-right: 1px solid #203B60;
      border-top : 0;
      border-bottom: 0;
    }
    .kline {
      display: block;
    }
    .trade-info {
      border-bottom: 1px solid #203B60;
    }
    .orderbar {
      border-right: 1px solid #203B60;
    }
    .contract {
      border-top: 1px solid #203B60;
      border-right: 1px solid #203B60
    }
    .trades {
      border-top: 1px solid #203B60;
    }
  }

}
