.confirmation {
  color: #E0ECFF;
    width: 375px;
    background: #0A172B;
    box-sizing: border-box;
    box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.5);
    border-radius: 4px;

    .confirm-title {
      font-size : 22px;
      display : flex;
      justify-content : space-between;
      align-items: center;
      height : 80px;
      padding : 0 24px;
      img {
        cursor : pointer;
      }
    }
    .confirm-legend {    
      display: flex;
      // justify-content: space-between;
      height: 93px;
      padding: 0 24px;
      background: linear-gradient(180deg, rgba(97, 122, 255, 0.1) 0%, rgba(97, 122, 255, 0) 100%);
      .direction-legend,.volume-legend {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 50%;
      }
      .direction-legend,.volume-legend{
        .Long {
          color: #59AE99;
          border: 1px solid #59AE99;
        }
        .Short{
          color: #CE5E5E;
          border: 1px solid #CE5E5E;
        }
        .before {
          margin-right: 40%;
        }
        .now {
          margin: 8px 0 0 0px;
          display: flex;
          .arrow {
            color: #93A1B8;
            margin-right: 12%;
          }
        }
      }
      .volume-legend{
        margin-left: 56px;
      }
    }
    .confirm-body {
      font-size : 14px;
      .confirm-row {
        padding : 0 24px;
        display : flex;
        justify-content : space-between;
        margin-top : 14px;
        .confirm-col {
          display: flex;
          color : #93A1B8;
        }
      }
    }
    .confirm-btn {
      display : flex;
      justify-content : center;
      margin: 32px 0 24px;
    }
}