.trade-info-content {
  display : flex;
  height: 96px;
  align-items: center;
  justify-content: space-between;
  padding: 0 32px;
  overflow-x: auto;
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
  &::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
  }
  .m-body {
    display : flex;
    align-items: center;
    justify-content: space-between;
    .m-col {
      display : flex;
      align-items: flex-start;
      flex-direction: column;
      margin-right : 48px;
      justify-content: space-between;
      white-space: nowrap;
      height : 46px;
      // align-items: center;
      .col-value {
        font-size : 20px;
        color : #E0ECFF;
        height :24px;
        white-space: nowrap;
      }
      .col-value.up {
        color : #59AE99;
      } 
      .col-value.down {
        color : #CE5E5E;
      }
    } 
  }
  .slide-icon {
   display: none;
  }
}

@media only screen and (max-width : 759px) {
  .trade-info-content {
    padding : 0 24px 0 24px;
    .slide-icon {
      position: absolute;
      display: block;
      background: #0A172B;
      height: 96px;
      display: flex;
      align-items: center;
    }
    .slide-icon.right {  
      right: 0px;
    }
    .slide-icon.left {
      left: 0px;
    }
  }
}